import React from 'react';
import './slideshow.css';

class SlideShow extends React.Component{
    constructor(props){
      super(props);
      this.state={img:1};
    }
    auto=()=> {
      this.timer=setInterval(() => {
        this.setState({img:this.state.img==4?1:this.state.img+1})
      }, 1500)
    }
    next=()=>{
      this.setState({img:this.state.img==4?1:this.state.img+1})
    }
    back=()=>{
      this.setState({img:this.state.img==1?4:this.state.img-1})
    }
    pause=()=>{
      clearInterval(this.timer);
     }
    render(){
      return(
        <div className="slider" >
          <img className="img" src={"img/slide"+this.state.img+".jpg" } alt="image1" />
          <a className="icon-back" onClick={this.back}><i className="fas fa-angle-left"></i></a>
          <a className="icon-next" onClick={this.next}><i className="fas fa-angle-right"></i></a>
          <button className="button auto rot-135" type="button" onClick={this.auto}>AUTO</button>
          <div class="pause"><button className="button pause1 rot-135" type="button" onClick= {this.pause}>PAUSE</button></div>
          
          
        </div>
      )
    }
      
  }
  export default SlideShow;